import React from 'react';
//import logo from './logo.svg';

import { BrowserRouter, Route, Link, Switch } from 'react-router-dom';
import ScrollToTop from './components/ScrollToTop.js';

import Home from './components/Home.js';
import Projects from './components/Projects.js';
import Articles from './components/Articles.js';
import About from './components/About.js';
import Contact from './components/Contact.js';

//Projects
import BalancingRobot from './components/projects/balancingRobot.js'
import Beatbox from './components/projects/beatbox.js'
import BigData from './components/projects/bigData.js'
import BlueNote from './components/projects/blueNote.js'
import Embla from './components/projects/embla.js'
import JoyStory from './components/projects/joyStory.js'
import LeapFrog from './components/projects/leapfrog.js'
import Pacman from './components/projects/pacman.js'
import Panaseer from './components/projects/panaseer.js'
import Pause from './components/projects/pause.js'
import PeoplePod from './components/projects/peoplePod.js'
import Reveng from './components/projects/reveng.js'
import Spiro from './components/projects/spiro.js'

// Articles
import HomeAutomation from './components/articles/homeAutomation.js'

import SocialBar from './components/SocialBar.js';

import {CSSTransition,TransitionGroup} from 'react-transition-group';

// Styles
import './css/App.css';
import './css/bootstrap.min.css';
import "animate.css/animate.min.css";

function App() {
  return (
    <BrowserRouter>
      <ScrollToTop>
        <Route render={({ location }) => (
          <div className="App">
            <header className="header-section">
              <div className="container">
                <div className="row justify-content-center">
                  <div className="col-lg-4 col-md-4">
                    <div className="logo">
                      <h2><Link to="/" className="site-logo">William Pepera</Link></h2>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 col-4">
                    <div style={{textAlign:'center'}}>
                      <nav className="main-menu row">
                        <ul>
                          <li><Link to="/projects" className={(location.pathname==='/projects')?"activePage":"inactivePage"}>Projects</Link></li>
                          <li><Link to="/articles" className={(location.pathname==='/articles')?"activePage":"inactivePage"}>Articles</Link></li>
                          <li><Link to="/about" className={(location.pathname==='/about')?"activePage":"inactivePage"}>About</Link></li>
                          <li><Link to="/contact" className={(location.pathname==='/contact')?"activePage":"inactivePage"}>Contact</Link></li>
                        </ul>
                      </nav>
                    </div>
                  </div>
                  <div className="col-lg-4 col-md-4 col-4">
                    <div style={{textAlign:'right'}}>
                      <SocialBar/>
                    </div>
                  </div>
                </div>
              </div>
            </header>

            <TransitionGroup>
              <CSSTransition
                  key={location.key}
                  timeout={800}
                  classNames='page'
                  >
                  <Switch location={location}>
                    <Route exact path="/" component={Home} />
                    <Route path="/about" component={About} />
                    <Route path="/contact" component={Contact} />
                    <Route exact path="/projects" component={Projects} />
                    <Route path="/balancingRobot" component={BalancingRobot} />
                    <Route path="/beatbox" component={Beatbox} />
                    <Route path="/bigData" component={BigData} />
                    <Route path="/blueNote" component={BlueNote} />
                    <Route path="/embla" component={Embla} />
                    <Route path="/joyStory" component={JoyStory} />
                    <Route path="/leapfrog" component={LeapFrog} />
                    <Route path="/pacman" component={Pacman} />
                    <Route path="/panaseer" component={Panaseer} />
                    <Route path="/pause" component={Pause} />
                    <Route path="/peoplePod" component={PeoplePod} />
                    <Route path="/reveng" component={Reveng} />
                    <Route path="/spiro" component={Spiro} />
                    <Route path="/articles" component={Articles} />
                    <Route path="/homeAutomation" component={HomeAutomation} />
                  </Switch>
              </CSSTransition>
            </TransitionGroup>
            <footer className="footer-section text-center">
              <div className="container">
                <div className="copyright">
                  Built with React.js       Copyright &copy; 2020
                </div>
              </div>
            </footer>
          </div>
        )}/>
      </ScrollToTop>
    </BrowserRouter>
  );
}

export default App;
