import React from 'react'; // Import the Component component from React
import {Link} from 'react-router-dom'; // Import the Link component
import '../css/App.css';
import Text from './projectPage/Text.js'

function ArticleThumbnail(props) {
  return (
    <Link to={props.link}>
      <div className="divider">
        <hr className="solid"/>
      </div>
      <div className="row justify-content-center">
        <div className={"image-wrapper "+props.imgCol} style={{margin:"0px 0px 0px 0px"}}>
            <img src={props.image} style={{height:"auto",padding:props.padding}} alt="default"/>
        </div>
        <div className={props.textCol}>
          <div className="row">
            <Text
             text={props.title}
             col={props.textCol}
             align="left"
             type="sectionOne-title"
             font="20px"
             margin="0px 0px 20px 20px"
            />
          </div>
          <div className="row">
            <Text
             text={props.text}
             col={props.textCol}
             align="left"
             type="text"
             font="15px"
             margin="0px 0px 0px 20px"
            />
          </div>
        </div>
      </div>
    </Link>
  );
}

export default ArticleThumbnail;
